import {
  BottomNavigation, BottomNavigationAction, Drawer, Paper,
} from '@mui/material';
import { useNavigate } from 'react-router-dom';
import CalendarMonthIcon from '@mui/icons-material/CalendarMonth';
import DataSaverOffIcon from '@mui/icons-material/DataSaverOff';
import AddCircleIcon from '@mui/icons-material/AddCircle';
import NotificationsIcon from '@mui/icons-material/Notifications';
import PersonIcon from '@mui/icons-material/Person';
import { useState } from 'react';
import PATH from '../../utils/constants/path';
import { NOTHING_IS_HERE_YET } from '../../utils/constants/common';
import AddScheduleDrawer from '../calender/addScheduleDrawer/AddScheduleDrawer';

function BottomBar({ value, setValue }) {
  const navigate = useNavigate();
  const [bottomDrawerOpen, setBottomDrawerOpen] = useState(false);
  return (
    <>
      <Paper
        sx={{
          position: 'fixed', bottom: 0, left: 0, right: 0,
        }}
        elevation={3}
      >
        <BottomNavigation
          showLabels
          value={value}
          onChange={(event, newValue) => {
            setValue(newValue);
          }}
        >
          <BottomNavigationAction label="" icon={<CalendarMonthIcon />} onClick={() => navigate(PATH.home)} />
          <BottomNavigationAction label="" icon={<DataSaverOffIcon />} onClick={() => navigate(PATH.analysis)} />
          <BottomNavigationAction label="" icon={<AddCircleIcon />} onClick={() => setBottomDrawerOpen(true)} />
          <BottomNavigationAction label="" icon={<NotificationsIcon />} onClick={() => alert(NOTHING_IS_HERE_YET)} />
          <BottomNavigationAction label="" icon={<PersonIcon />} onClick={() => navigate(PATH.mypage)} />
          {/* <BottomNavigationAction label="Archive" icon={<ArchiveIcon />} /> */}
        </BottomNavigation>
      </Paper>
      <Drawer
        open={bottomDrawerOpen}
        anchor="bottom"
        onClose={() => setBottomDrawerOpen(false)}
      >
        {/*         ? */}
        <AddScheduleDrawer setBottomDrawerOpen={setBottomDrawerOpen} />
      </Drawer>
    </>
  );
}
export default BottomBar;
